import React, { Component } from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"; //fontaweresome
import { faClone } from "@fortawesome/free-regular-svg-icons";//
import { faUserTag } from "@fortawesome/free-solid-svg-icons";

let nTimer;
export default class p5 extends Component{
    constructor(props){
        super(props);
        this.state={
            search:""
        }
    }
    inputSearchStart(val){
        this.setState({search:val});
        if(nTimer){clearTimeout(nTimer);}
        nTimer = setTimeout(() => {
            this.props.apiFunction.calendar_share("search",0,"search",this.state.search);
            console.log(this.state.search)
        }, 100)
    }
    render(){
        return(
            <main key={"settingP5"} className={this.props.element.page === 5 ? 'settingBodyWrap popup_toggle_effect' : 'settingBodyWrap popup_toggle_effect_de'}>
                <section className="settingBody">
                    <h2></h2>
                    <div className="libraryMenu flex-jus-between">
                        <div className="tags flex">
                            <div className="flex-jus-center active"></div>
                            <div className="flex-jus-center"></div>
                            <div className="flex-jus-center"></div>
                        </div>
                        <div className="search">
                            <input type="text" placeholder="Key" className="removeCss" value={this.state.search} onChange={(e) => this.inputSearchStart(e.target.value)}/>
                        </div>
                    </div>
                    <div className="flex calendarSearchResult">
                        {this.props.calendarSearchResult.map((data) =>
                            <div className="CalendarCard flex" key={data.key + "calendarSearch" + this.state.search}>
                                <div className="flex" style={{width:"100%"}}>
                                    <div className="imageContainer" style={{color:data.color}}>{data.name.slice(0,1)}</div>
                                    <div className="dataContainer">
                                        <div className="nameBox flex-jus-between scroll-x">{data.name}</div>
                                        <div className="authorBox flex-jus-between scroll-x">: {data.author_name}</div>
                                        <div className="descriptionBox flex-jus-between scroll-y">{data.description}</div>
                                        <div className="buBo flex">
                                            {data.cloneBool &&
                                                <div onClick={() => this.props.action.ConfirmationPopup("","","" + data.name + "",data.id,"clone")}><FontAwesomeIcon icon={faClone} style={{paddingRight:"5px"}}/></div>
                                            }
                                            <div onClick={() => this.props.action.ConfirmationPopup("","","" + data.name + "",data.id,"follow")}><FontAwesomeIcon icon={faUserTag} style={{paddingRight:"5px"}}/></div>
                                        </div>
                                        <div className="data flex">
                                            <div>: {data.scheduleCount}</div>
                                            <div>: {data.taskCount}</div>
                                            <div>: {data.examCount}</div>
                                            <div>: {data.change_schedulesCount}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )}
                        {this.props.calendarSearchResult.length < 1 &&
                            <div></div>
                        }
                    </div>
                </section>
                
            </main>
        )
    }
}